<template>
  <div>
    <section class="hero is-primary landing is-fullheight">
      <div class="hero-body">
        <div class="container">
          <h1 class="title">
            Roy's Portfolio
          </h1>
          <h2 class="subtitle">
            Software Engineering Student
          </h2>

          <div class="contact">
            <div class="contact-item">
              <img src="./../assets/images/github.png"><a v-on:click="click(links.github)">r-portas</a>
            </div>
            <div class="contact-item">
              <img src="./../assets/images/twitter.png"><a v-on:click="click(links.twitter)">@RoyPortas</a>
            </div>
            <div class="contact-item">
              <img src="./../assets/images/linkedin.png"><a v-on:click="click(links.linkedin)">Roy Portas</a>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'hero',
  data() {
    return {
      links: {
        github: 'https://github.com/r-portas',
        twitter: 'https://twitter.com/RoyPortas',
        linkedin: 'https://au.linkedin.com/in/royportas',
      },
    };
  },
  methods: {
    click(url) {
      this.$ua.trackEvent('Link', url);
      window.open(url);
    },
  },
};
</script>

<style scoped>

.contact {
  margin-top: 100px;
}

.contact-item {
  margin: 5px;
  display: flex;
  align-items: center
}

.contact-item > img {
  height: 20px;
  margin-right: 10px;
}

.contact-item > a {
  text-decoration: underline;
}

.landing {
  background-image: url("https://static.pexels.com/photos/6579/pexels-photo.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
}

</style>



